import React, { useState } from 'react';
import Accordion from '../../../Atoms/Accordion';
import Button from '../../../Atoms/Button';
import { GoPlus, GoListUnordered } from 'react-icons/go';
import InputElement from '../../../Atoms/InputElement';
import HoverContainer from '../../../Atoms/hoverContainer';
import commonAction from '../../../../Store/commonActivity/action';

const Flight = props => {
  const {
    Criteria = [],
    callback = () => {},
    CriteriaName = '',
    isEditPage,
    InteractionComponent = () => null,
    InteractionComponentData = '',
    label = '',
    eventType = '',
  } = props;
  const [isAddpageOpened, setAddSignalpageOpen] = useState(false);
  const [isAddAttributeOpened, setAddAtrributeOpen] = useState(false);
  const [isNewSignal, setNewSignal] = useState(true);
  const [currentSignal, setAddSignal] = useState({});
  const [AttrList, setAttrList] = useState(null);
  const [currentAttr, setcurrentAttr] = useState({});
  const [isAttrEnabled, setAttrEnable] = useState(false);

  const [isNewAttribute, setNewAttribute] = useState(true);
  let count = 0;
  const currentType = eventType.toLowerCase();
  const mapData = new Map();
  mapData.set('transactional', ['scheduling', 'reinitiate']);
  mapData.set('stateless', ['exitcriteria']);
  mapData.set('temporal', []);
  mapData.set('passthrough', ['exitcriteria']);
  mapData.set('stateful', []);
  let isSelected = false;

  const editSignal = signal => {
    if (signal === null) {
      setAddSignal({});
      setNewSignal(true);
    } else {
      setNewSignal(false);
      setAddSignal(signal);
    }

    setAddSignalpageOpen(true);
  };

  const deleteSignal = signal => {
    const newCriteria = Criteria.filter(val => {
      if (val.iaoSignalName === signal.iaoSignalName) return false;
      return true;
    });
    if (!newCriteria.length) {
      setAttrEnable(false);
    }
    callback({ ...props, InteractionComponentData: newCriteria });
  };

  const selectSignal = signal => {
    Criteria.forEach(val => {
      delete val.selected;
    });

    if (signal.selected) {
      signal.selected = false;
      setAttrList(null);
    } else {
      signal.selected = true;
      signal.domain = 'Flight';
      setAttrList(signal);
      setAttrEnable(true);
      setAddSignal(signal);
    }
  };

  const AddSignal = ({ callback, signal }) => {
    if (!signal) {
      signal = { iaoSignalName: '' };
    }
    const [signalname, setSignalName] = useState(signal.iaoSignalName);

    return (
      <div className="add-signal-container">
        <div>
          <div className="signal-name">Signal name</div>
          <InputElement
            className="signal-input"
            value={signalname}
            onChange={e => setSignalName(e.target.value)}
          />
        </div>
        <div className="signal-controller">
          <Button
            className="cancel-signal"
            onClick={() => {
              callback('cancel');
            }}
          >
            Cancel
          </Button>
          <Button
            className="add-signal-button"
            onClick={() => {
              if (!signalname) {
                commonAction.openAlert({
                  title: 'Warning',
                  text: 'Signal name should not be empty',
                });
                return;
              }
              callback('add', { old: signal.iaoSignalName, new: signalname });
            }}
          >
            Add
          </Button>
        </div>
      </div>
    );
  };

  const UI = Criteria.map((entryData, index) => {
    const domain = (entryData.domain && entryData.domain.toLowerCase()) || '';

    if (domain === 'flight') {
      count++;
    }
    return (
      domain === 'flight' && (
        <div
          className={`criteria-body ${
            entryData.selected ? 'active-signal' : ''
          }`}
          onClick={() => {
            selectSignal(entryData);
          }}
        >
          {count && (
            <HoverContainer
              disableHover={isEditPage ? false : true}
              editHandler={e => {
                e.stopPropagation();
                editSignal(entryData);
              }}
              deleteHandler={e => {
                e.preventDefault();
                e.stopPropagation();
                deleteSignal(entryData);
              }}
            >
              <div className="criteria-list">
                {entryData.iaoSignalName}
                <div className="hover-container"></div>
              </div>
            </HoverContainer>
          )}
        </div>
      )
    );
  });

  const closeAddSignal = (condition, signal) => {
    if (condition === 'cancel') {
      setAddSignalpageOpen(false);
      return;
    }
    let newCriteria = Criteria;
    if (condition === 'add') {
      if (isNewSignal) {
        let nameAlreadyExist = false;
        Criteria.forEach(val => {
          if (val.iaoSignalName === signal.new) {
            nameAlreadyExist = true;
          }
        });

        if (nameAlreadyExist) {
          commonAction.openAlert({
            text: 'Warning',
            text: 'Signal Already Exists',
          });
          return;
        }

        const newData = {
          metadata: {},
          iaoSignalName: signal.new,
          domain: 'Flight',
        };
        newCriteria.push(newData);
      } else {
        newCriteria = Criteria.map(val => {
          if (val.iaoSignalName === signal.old) {
            val.iaoSignalName = signal.new;
          }
          return val;
        });
      }
    }

    setAddSignalpageOpen(false);
    callback({ ...props, InteractionComponentData: newCriteria });
  };

  // attribute starts

  const editAttribute = (attr, val) => {
    if (attr === null) attr = '';
    if (val === null) val = '';
    setcurrentAttr({ attr, val });
    setAddAtrributeOpen(true);
  };

  const deleteAttribute = attr => {
    let newAttribute = currentSignal.metadata || {};

    newAttribute &&
      Object.keys(newAttribute).map(val => {
        if (val === attr) {
          delete newAttribute[val];
        }
      });

    let newCriteria = Criteria;

    newCriteria = Criteria.map(val => {
      if (val.iaoSignalName === currentSignal.iaoSignalName) {
        val.metadata = newAttribute;
      }
      return val;
    });
    setAddSignal({ ...currentSignal, metadata: newAttribute });
    callback({ ...props, InteractionComponentData: newCriteria });
  };

  const AttributeList =
    AttrList &&
    Object.keys(AttrList.metadata).map((attr, index) => {
      return (
        <div className={`criteria-body`}>
          {count && (
            <HoverContainer
              disableHover={isEditPage ? false : true}
              deleteHandler={e => {
                e.stopPropagation();
                deleteAttribute(attr);
              }}
              editHandler={e => {
                e.stopPropagation();

                setNewAttribute(false);

                editAttribute(attr, AttrList.metadata[attr]);
              }}
            >
              <div className="criteria-list">{attr}</div>
            </HoverContainer>
          )}
        </div>
      );
    });

  const closeAddAttribute = (condition, data) => {
    if (condition === 'cancel') {
      setAddAtrributeOpen(false);
      return;
    }
    let newAttribute = currentSignal.metadata || {};
    if (condition === 'add') {
      newAttribute &&
        Object.keys(newAttribute).map(val => {
          if (val === data.old.attr) {
            newAttribute[data.new.attr] = data.new.value;
            if (!(data.old.attr === data.new.attr)) {
              delete newAttribute[val];
            }
          }
        });
    }

    if (isNewAttribute) {
      if (data.new.val) {
        newAttribute[data.new.attr] = data.new.val;
      } else {
        newAttribute[data.new.attr] = '';
      }
    }

    setAddAtrributeOpen(false);

    let newCriteria = Criteria;

    newCriteria = Criteria.map(val => {
      if (val.iaoSignalName === currentSignal.iaoSignalName) {
        val.metadata = newAttribute;
      }
      return val;
    });
    setAddSignal({ ...currentSignal, metadata: newAttribute });
    callback({ ...props, InteractionComponentData: newCriteria });
  };

  return (
    <div className="scroll signal-container">
      <div className="signals">
        <div className="criteria-header">
          {isEditPage && (
            <>
              <div className="header">Signals</div>
              <div className="add-signal">
                <GoPlus className="add-button-icon" />
                <Button
                  className="add-signal-button"
                  onClick={() => {
                    setAddSignal({});
                    editSignal(null);
                  }}
                >
                  Add Signal
                </Button>
              </div>
            </>
          )}
        </div>
        {isAddpageOpened ? (
          <AddSignal callback={closeAddSignal} signal={currentSignal} />
        ) : (
          <div className="criteria-list-container">{UI}</div>
        )}
      </div>
      {isAttrEnabled && (
        <div className="signals attribute-container">
          <div className="criteria-header">
            {isEditPage && (
              <>
                <div className="header">Attributes</div>
                <div className="add-attribute-button-container">
                  <GoPlus className="add-button-icon" />
                  <Button
                    className="add-attribute-button"
                    onClick={() => {
                      setNewAttribute(true);
                      editAttribute(null, '');
                    }}
                  >
                    Add Attributes
                  </Button>
                </div>
              </>
            )}
          </div>
          {isAddAttributeOpened ? (
            <AddAttribute
              callback={closeAddAttribute}
              attribute={currentAttr}
            />
          ) : (
            <div className="criteria-list-container attribute-list-container scroll">
              {AttributeList}
            </div>
          )}
        </div>
      )}
    </div>
  );
};

const AddAttribute = ({ callback, attribute = { attr: '', val: '' } }) => {
  const [attributename, setAttributeName] = useState(attribute.attr);
  const [attributevalue, setAttributeValue] = useState(attribute.val);
  if (attributevalue === null) {
    setAttributeValue('');
  }
  return (
    <div className="add-attribute-container">
      <div>
        <div className="attribute-name">Attribute Name</div>
        <InputElement
          className="attribute-input"
          value={attributename}
          onChange={e => setAttributeName(e.target.value)}
        />
      </div>
      <div>
        <div className="attribute-name">Attribute Value</div>
        <InputElement
          className="attribute-input"
          placeholder={'Expression (OR) URL'}
          value={attributevalue}
          onChange={e => setAttributeValue(e.target.value)}
        />
      </div>
      <div className="attribute-controller">
        <Button
          className="cancel-attribute"
          onClick={() => {
            callback('cancel');
          }}
        >
          Cancel
        </Button>
        <Button
          className="add-attribute-button"
          onClick={() => {
            if (!attributename) {
              commonAction.openAlert({
                title: 'Warning',
                text: 'Attribute name should not be empty',
              });
              return;
            }
            callback('add', {
              old: attribute,
              new: { attr: attributename, val: attributevalue },
            });
          }}
        >
          Add
        </Button>
      </div>
    </div>
  );
};

export default Flight;
