import React, { Component } from 'react';
import EventList from './View-Instance/event-list';
import Button from '../../Atoms/Button';
import { GoPlus, GoListUnordered } from 'react-icons/go';
import './index.scss';
import AddInstance from './Add-Instance/addInstance';
import Popup from '../../Atoms/popup/popup';

class BusinessEvents extends Component {
  header = {
    ActiveEventInstanceHeader: {
      Component: EventList,
      headerName: 'Business Event Catlalog',
      buttonName: 'Add Instance',
      navigateTo: 'AddInstanceHeader',
      Icon: GoPlus,
    },
    AddInstanceHeader: {
      headerName: 'Business Event Catlalog',
      Component: AddInstance,
      buttonName: 'View Instance',
      navigateTo: 'ActiveEventInstanceHeader',
      Icon: GoListUnordered,
    },
  };

  state = { currentPage: this.header.ActiveEventInstanceHeader };
  navigate = e => {
    Object.keys(this.header).forEach(val => {
      if (e.target.innerText === this.header[val].buttonName) {
        const currentPage = this.header[val].navigateTo;
        this.setState({ currentPage: this.header[currentPage] });
      }
    });
  };
  render() {
    const { Icon, Component, buttonName, headerName } = this.state.currentPage;
    return (
      <div className="main business-event-page">
        {/* <Popup
          isOpen={true}
          content={{ title: 'hello', children: 'ree', buttonText: 'sd' }}
        /> */}
        <div className="Events">
          <div className="EventManagementHeader">
            <div className="EventManagementwrap">
              <div className="EventManagementText">{headerName}</div>
              <div className="Event-header-right-content">
                {/* <div className="event-header-create">
                  <Icon className="event-create-icon" />
                  <Button
                    className="CreateEventTemplate"
                    onClick={this.navigate}
                  >
                    {buttonName}
                  </Button>
                </div> */}
              </div>
            </div>
          </div>
        </div>
        <div className="config-container">{<Component />}</div>
      </div>
    );
  }
}

export default BusinessEvents;
