import React, { Component } from 'react';
import './rule.scss';
import { GoPlus } from 'react-icons/go';
import commonAction from '../../../../../Store/commonActivity/action';
import { Allimages } from '../../../../../Utils/imageRetriver';
import InputElement from '../../../../Atoms/InputElement';
import Button from '../../../../Atoms/Button';
import HoverContainer from '../../../../Atoms/hoverContainer';

export default class Rule extends Component {
  state = {
    newRuleMapping: {
      ruleId: '',
      ruleConstruct: '',
    },
    tempMapping: {},
  };
  changeHandler = (e, condition) => {
    const { newRuleMapping } = this.state;
    newRuleMapping[condition] = e.target.value;
    this.setState({ newRuleMapping });
  };
  editRule = value => {
    this.setState({
      newRuleMapping: value,
      tempMapping: JSON.parse(JSON.stringify(value)),
    });
  };
  deleteRule = value => {
    // this.setState({ newRuleMapping: false });
    let ruleMapping = this.props.InteractionComponentData || [];
    ruleMapping = ruleMapping.filter(rule => rule.ruleId !== value.ruleId);
    this.props.callback({
      label: this.props.label,
      InteractionComponentData: ruleMapping,
    });
  };

  addRule = () => {
    const { newRuleMapping } = this.state;
    let ruleMapping = this.props.InteractionComponentData || [];
    if (!newRuleMapping.ruleId) {
      commonAction.openAlert({
        title: 'warning',
        text: 'Rule Id must be filled',
      });
      return;
    }
    if (!newRuleMapping.ruleConstruct) {
      commonAction.openAlert({
        title: 'warning',
        text: 'RuleConstruct must be filled',
      });
      return;
    }
    // check whether id already available
    console.log(this.state.tempMapping, 'fdasf');
    let isNew = true;
    ruleMapping = ruleMapping.map(val => {
      console.log(val, this.state.tempMapping, 'fdasf');
      if (
        this.state.tempMapping.ruleId &&
        val.ruleId === this.state.tempMapping.ruleId
      ) {
        isNew = false;
        return this.state.newRuleMapping;
      }
      return val;
    });
    if (isNew) {
      ruleMapping.push(this.state.newRuleMapping);
    }
    this.setState(
      {
        ruleMapping: ruleMapping,
        newRuleMapping: { ruleId: '', ruleConstruct: '' },
        tempMapping: {},
      },
      () => {
        this.props.callback({
          label: this.props.label,
          InteractionComponentData: ruleMapping,
        });
      }
    );
  };

  render() {
    return (
      <>
        <div className="width-50">
          <div className="RuleBox">
            {/* {this.state.currentView === 'EDIT' ? (
          <Rule
            instanceData={this.props.instanceData}
            page={'EDIT'}
          />
        ) : (<RuleDisplay/>)} */}
            <div className="Rule">
              <div className="RuleID">
                Rule ID
                <div className="RuleID-inputfields">
                  <InputElement
                    placeholder="XD0428"
                    onChange={e => {
                      this.changeHandler(e, 'ruleId');
                    }}
                    value={this.state.newRuleMapping.ruleId}
                  />
                </div>
              </div>
              <div className="RuleName">
                Rule Name
                <div className="RuleName-inputfields">
                  <InputElement
                    placeholder="Rule Name"
                    onChange={e => {
                      this.changeHandler(e, 'ruleConstruct');
                    }}
                    value={this.state.newRuleMapping.ruleConstruct}
                  />
                </div>
              </div>
              <div className="AddRuleBox" onClick={this.addRule}>
                <Button className="AddRuleName">
                  Add
                  <GoPlus className="AddRuleIcon" />
                </Button>
              </div>
            </div>
            {/* {this.props.businesseventTemplateData.map((value, key) => ( */}

            {this.props.InteractionComponentData &&
              this.props.InteractionComponentData.map((val, index) => {
                if (!val.ruleId) return null;
                return (
                  <div
                    key={index}
                    // onClick={() =>
                    //   this.editRule(JSON.parse(JSON.stringify(val)))
                    // }
                    className="RuleDisplay"
                  >
                    <HoverContainer
                      disableHover={this.props.isEditPage ? false : true}
                      editHandler={() =>
                        this.editRule(JSON.parse(JSON.stringify(val)))
                      }
                      deleteHandler={() => {
                        this.deleteRule(JSON.parse(JSON.stringify(val)));
                      }}
                    >
                      <div className="RuleIDDisplay">{val.ruleId}</div>
                      <div className="RuleNameDisplay">{val.ruleConstruct}</div>
                    </HoverContainer>
                  </div>
                );
              })}

            {/* ))} */}
          </div>
        </div>
      </>
    );
  }
}
