import React, { Component } from 'react';
import './Schedulling.scss';
import InputElement from '../../../../Atoms/InputElement';
import { Dropdown } from '../../../../Atoms/DropdownComponent';
class Scheduling extends Component {
  state = {
    isNewScheduling: true,
    schedulingCondition: this.props.InteractionComponentData,
    addschedule: false,
  };

  changeHandler = (event, condition) => {
    const { InteractionComponentData = {} } = this.props;
    if (condition === 'operator') {
      InteractionComponentData[condition] = event.title;
      this.list.forEach(val => {
        val.selected = false;
        if (val.title === event.title) val.selected = true;
      });
    } else if (condition === 'scheduleKey') {
      InteractionComponentData[condition] = event.target.value;
    } else {
      const input = event.target.value;
      if (/^\d*$/.test(input)) {
        InteractionComponentData[condition] = event.target.value;
      }
    }
    this.props.callback({
      label: this.props.label,
      InteractionComponentData,
    });
  };

  list = [
    { title: 'ADD', selected: false },
    { title: 'SUBTRACT', selected: false },
  ];

  render() {
    const {
      scheduleKey = '',
      snoozesTimeValueMins = 1,
      snoozesTimeIncrementValueMins = 0,
      operator = 'ADD',
    } = this.props.InteractionComponentData || {};
    return (
      <>
        <div className="Schedulling-attributes ">
          <div className="Schedulling-attributes ">
            <div className="Schedulling-heading">Attribute Name</div>
            <div className="Schedulling-inputfields">
              <InputElement
                value={scheduleKey}
                type="text"
                onChange={e => this.changeHandler(e, 'scheduleKey')}
              />
            </div>

            <div className="Schedulling-Dropdown-box">
              <div className="Schedulling-Snooze-time-heading">Operator</div>
              <div
                className="Schedulling-Snooze-time-inputfields"
                style={{ width: '40%' }}
              >
                <Dropdown
                  list={this.list}
                  toggleItem={currentlist =>
                    this.changeHandler(currentlist, 'operator')
                  }
                  title={operator}
                />
              </div>
            </div>

            <div className="Schedulling-Snooze-and-IncrementalValue-box">
              <div className="Schedulling-Snooze-box">
                <div className="Schedulling-Snooze-time-heading">
                  Snooze time
                </div>
                <div className="Schedulling-Snooze-time-inputfields">
                  <InputElement
                    value={snoozesTimeValueMins}
                    type={'text'}
                    onChange={e =>
                      this.changeHandler(e, 'snoozesTimeValueMins')
                    }
                  />
                  <span className="minute">Min</span>
                </div>
              </div>

              <div className="Schedulling-Snooze-time-box">
                <div className="Schedulling-Snooze-time-heading">
                  Incremental Value
                </div>
                <div className="Schedulling-Snooze-time-inputfields">
                  <InputElement
                    value={snoozesTimeIncrementValueMins}
                    type={'text'}
                    onChange={e =>
                      this.changeHandler(e, 'snoozesTimeIncrementValueMins')
                    }
                  />
                  <span className="minute">Min</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Scheduling;
