import React, { Component } from 'react';

import Flight from '../Domain/flight';
import Pax from '../Domain/Pax';
import Crew from '../Domain/Crew';
import Bag from '../Domain/Crew';
import Engg from '../Domain/Engg';
import Ground from '../Domain/Engg';
import Button from '../../../Atoms/Button';
import { connect } from 'react-redux';
import { eventConfiguration } from '../../../../Store/EventConfigurationPage/eventConfig.action';
import Scheduling from '../Domain/Scheduling/schedulling';
import Rule from '../Domain/Rule/rule';
import MappingSignal from '../Domain/mapping';
import commonAction from '../../../../Store/commonActivity/action';
import { businesseventConfiguration } from '../../../../Store/BusinessEventsPage/businessConfig.action';

class UpdateSignal extends Component {
  state = {
    instanceData: this.props.instanceData,
  };

  ChooseTemplate = props => {
    const { active } = props;
    let Component = null;
    if (active.toLowerCase() === 'flight') {
      Component = <Flight {...props} />;
    }
    if (active.toLowerCase() === 'pax') {
      Component = <Pax {...props} />;
    }
    if (active.toLowerCase() === 'crew') {
      Component = <Crew {...props} />;
    }
    if (active.toLowerCase() === 'bag') {
      Component = <Bag {...props} />;
    }
    if (active.toLowerCase() === 'engg') {
      Component = <Engg {...props} />;
    }
    if (active.toLowerCase() === 'ground') {
      Component = <Ground {...props} />;
    }

    if (props.label === 'Rule') {
      Component = <Rule {...props} />;
    }

    if (props.label === 'Schedule') {
      Component = <Scheduling {...props} />;
    }
    if (Component) {
      return (
        <>
          {Component}
          {props.isEditPage && (
            <div className="save-container">
              <Button
                onClick={() => {
                  //    this.props.getEvent_Template();
                  this.props.closeEdit();
                }}
              >
                cancel
              </Button>
              <Button
                onClick={() => {
                  const criteria = [
                    'entryCriteria',
                    'exitCriteria',
                    'reinitiateCriteria',
                  ];
                  const instanceData = JSON.parse(
                    JSON.stringify(this.state.instanceData)
                  );
                  criteria.forEach(val => {
                    if (!instanceData[val]) return;
                    let temp = [...instanceData[val]];
                    temp = temp.map(data => {
                      if (data.selected) {
                        delete data.selected;
                      }
                      return data;
                    });
                    instanceData[val] = temp;
                  });
                  const parentData = this.props.instanceData;

                  if (!parentData.businessEvent) {
                    commonAction.openAlert({
                      title: 'warning',
                      text: 'Please Enter Event Name',
                    });
                    return;
                  }
                  if (!parentData.eventDescription) {
                    commonAction.openAlert({
                      title: 'warning',
                      text: 'Please Enter Event Description',
                    });
                    return;
                  }
                  if (!parentData.eventType) {
                    commonAction.openAlert({
                      title: 'warning',
                      text: 'Please Select type',
                    });
                    return;
                  }

                  instanceData.eventType = parentData.eventType;
                  instanceData.businessEvent = parentData.businessEvent;
                  instanceData.eventDescription = parentData.eventDescription;

                  this.props.updateInstance(instanceData);
                  this.props.closeEdit();
                }}
              >
                save
              </Button>
            </div>
          )}
        </>
      );
    }
    return null;
  };

  selectedData = data => {
    //,, this.setState({})
    const instanceData = { ...this.state.instanceData };

    if (
      data.label === 'Schedule' &&
      Object.keys(data.InteractionComponentData).length
    ) {
      instanceData['schedulingCondition'] = data.InteractionComponentData;
    }

    if (data.label === 'Rule') {
      instanceData['ruleMapping'] = data.InteractionComponentData;
    }
    if (data.label !== 'Rule' && data.label !== 'Schedule') {
      instanceData[data.CriteriaName] = data.InteractionComponentData;
    }
    //instanceData[data.CriteriaName].forEach((val)=>{val.meta})

    if (data.label === 'Rule') {
      this.props.updateState(instanceData);
    } else {
      this.setState({ instanceData });
    }
    //  this.setState({ instanceData });

    //  this.props.callback(data);
  };

  render() {
    const {
      entryCriteria = [],
      exitCriteria = [],
      reinitiateCriteria = [],
      schedulingCondition = {},
      ruleMapping = [],
      subscription = [],
      eventType = '',
    } = this.state.instanceData;
    const { label, active = '' } = this.props.tabData;
    const isEditPage = this.props._props.isEditPage;
    const callback = this.props._props.isEditPage
      ? this.selectedData
      : () => {};

    const templateProps = { eventType, active, callback, isEditPage, label };

    if (label === 'Entry') {
      return this.ChooseTemplate({
        ...templateProps,
        Criteria: entryCriteria,
        InteractionComponent: MappingSignal,
        InteractionComponentData: entryCriteria,
        CriteriaName: 'entryCriteria',
      });
    }

    if (label === 'Exit') {
      return this.ChooseTemplate({
        ...templateProps,
        InteractionComponent: MappingSignal,
        InteractionComponentData: {},
        Criteria: exitCriteria,
        CriteriaName: 'exitCriteria',
      });
    }

    if (label === 'Reinstate') {
      return this.ChooseTemplate({
        ...templateProps,
        InteractionComponent: MappingSignal,
        InteractionComponentData: {},
        Criteria: reinitiateCriteria,

        CriteriaName: 'reinitiateCriteria',
      });
    }

    if (label === 'Schedule') {
      return this.ChooseTemplate({
        ...templateProps,
        Criteria: entryCriteria,
        InteractionComponent: Scheduling,
        InteractionComponentData: schedulingCondition,
        CriteriaName: 'scheduling',
      });
    }
    if (label === 'Rule') {
      return this.ChooseTemplate({
        ...templateProps,
        Criteria: subscription,
        InteractionComponent: Rule,
        InteractionComponentData: ruleMapping,
        CriteriaName: 'rule',
      });
    }

    return null;
  }
}
const mapStateToProps = state => {};
const actionCreator = {
  updateInstance: businesseventConfiguration.updateBusiness_Template,
  getEvent_Template: businesseventConfiguration.getBusiness_Event_Template,
};

export default connect(mapStateToProps, actionCreator)(UpdateSignal);
