import React, { Component } from 'react';
import UpdateSignal from '../Signal-list/index';
import MultiTabs from '../../../Molecules/multi-tabs';
import InputElement from '../../../Atoms/InputElement';
import { Dropdown } from '../../../Atoms/DropdownComponent';
import './criteria.scss';
import BusinessEvents from '..';

class Criteria extends Component {
  state = { instanceData: JSON.parse(JSON.stringify(this.props.instanceData)) };

  updateValue = instanceData => {
    this.setState({ instanceData }, () => {
      console.log(this.state.instanceData, 'criteriastate');
    });
  };

  updateState = instanceData => this.setState({ instanceData }, () => {});

  listComponent = props => {
    return (
      <UpdateSignal
        {...this.props}
        {...props}
        instanceData={this.state.instanceData}
        updateState={this.updateState}
        callback={this.selectedData}
      />
    );
  };

  tabList = [
    {
      label: 'Entry',
      childtab: ['FLIGHT', 'PAX', 'CREW', 'BAG', 'ENGG', 'GROUND'],
      active: 'FLIGHT',
      Component: this.listComponent,
    },
    {
      label: 'Exit',
      childtab: ['FLIGHT', 'PAX', 'CREW', 'BAG', 'ENGG', 'GROUND'],
      active: 'FLIGHT',
      Component: this.listComponent,
    },
    {
      label: 'Reinstate',
      childtab: ['FLIGHT', 'PAX', 'CREW', 'BAG', 'ENGG', 'GROUND'],
      active: 'FLIGHT',
      Component: this.listComponent,
    },
    {
      label: 'Schedule',
      childtab: [],
      active: 'FLIGHT',
      Component: this.listComponent,
    },
    {
      label: 'Rule',
      childtab: [],
      active: 'FLIGHT',
      Component: this.listComponent,
    },
  ];

  saveCriteria = () => {};

  render() {
    if (!this.state.instanceData['schedulingCondition']) {
      const instanceData = this.state.instanceData;
      instanceData['schedulingCondition'] = {
        scheduleKey: '',
        snoozesTimeValueMins: 1,
        snoozesTimeIncrementValueMins: 0,
        operator: 'ADD',
      };
      this.setState({ instanceData });
    }

    const isEditPage = this.props.page === 'EDIT' ? true : false;

    return (
      <div className="businessevent-criteria">
        {isEditPage && (
          <AddEventInputFields
            instanceData={this.state.instanceData}
            callback={this.updateValue}
          />
        )}
        <div className="criteria-list">
          <MultiTabs
            className="event-edit"
            tabData={this.tabList}
            isEditPage={isEditPage}
          />
        </div>
      </div>
    );
  }
}

export default Criteria;

export const AddEventInputFields = ({ instanceData, callback }) => {
  const {
    businessEvent = '',
    eventDescription = '',
    eventType = 'Stateful',
  } = instanceData;
  let list = [
    { title: 'Transactional', selected: false },
    { title: 'Temporal', selected: false },
    { title: 'Stateless', selected: false },
    { title: 'Passthrough', selected: false },
    { title: 'Stateful', selected: false },
  ];
  list = list.map(val => {
    if (val.title === eventType) val.selected = true;
    return val;
  });
  const updateValue = (value, key) => {
    const data = instanceData;
    data[key] = value;
    callback(data);
  };

  return (
    <>
      <div className="addEvent">
        <div className="addEventText">Add Event</div>
        <div className="bottomline"></div>

        <div className="eventDetails">
          <div className="Name">Event Name</div>
          <div className="eventinput">
            <InputElement
              value={businessEvent}
              onChange={e => updateValue(e.target.value, 'businessEvent')}
              placeholder="Enter"
            />
          </div>
        </div>
        <div className="eventDetails">
          <div className="Name">Description</div>
          <div className="eventinput">
            <textarea
              value={eventDescription}
              onChange={e => updateValue(e.target.value, 'eventDescription')}
              placeholder="Enter"
              type="text"
              className="description"
            ></textarea>
          </div>
        </div>
        <div className="eventDetails">
          <div className="Name">Type</div>
          <div className="eventinput" style={{ width: '40%' }}>
            <Dropdown
              title={eventType}
              list={list}
              toggleItem={item => {
                updateValue(item.title, 'eventType');
              }}
            />
          </div>
        </div>
      </div>
    </>
  );
};
